<template>
  <div class="dashboard" style="position: relative;height: 100%;width: 100%;background-color: #f0f2f5;padding: 0;">
      <el-row>
        <el-col>
          <el-aside :style="{width:ee+'px'}" ref="scrollBar">
            <!--@open="handleOpen" @close="handleClose"-->
            <el-menu :collapse="isCollapse" :default-active="activeIndex" :router="true" unique-open class="el-menu-vertical-demo" background-color="#304156" text-color="#fff" active-text-color="#ffd04b">
              <template v-for="(item,index) in this.routes" v-if="!item.hidden">
                    <el-submenu :key="index" :index="index+''">
                      <template slot="title">
                        <!--<i :class="item.iconCls"></i>-->
                        <i class="el-icon-edit"></i>
                        <span slot="title">{{item.meta.title}}</span>
                      </template> 
                      <el-menu-item v-for="(child,index) in item.children" :index="child.path" :key="child.path">{{child.meta.title}}</el-menu-item>
                    </el-submenu>
              </template>
            </el-menu>
          </el-aside>
        </el-col>
        <el-col>
          <el-container class="main-container" :style="{'margin-left':ee+'px'}">
            <div style="position: relative;margin-top:-8px ;margin-left: -7px;">
              <el-card class="header-card">    
                <div class="humbger-container">
                    <i class="el-icon-s-fold" v-if="!isCollapse" @click="collapse" style="font-size: 25px;"></i>
                    <i class="el-icon-s-unfold" v-if="isCollapse" @click="collapse" style="font-size: 25px;"></i>
                </div>
                <div class="breadcrumb-container">
                  <breadcrumb></breadcrumb>
                </div>
                <div class="avatar-container">
                  <el-dropdown>
                    <img src="../../static/moon.png" class="user-avatar"/>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item>黄金糕</el-dropdown-item>
                      <el-dropdown-item>狮子头</el-dropdown-item>
                      <el-dropdown-item>螺蛳粉</el-dropdown-item>
                      <el-dropdown-item disabled>双皮奶</el-dropdown-item>
                      <span style="display:block;" @click="logout">
                        <el-dropdown-item divided>注销</el-dropdown-item>
                      </span>
                    </el-dropdown-menu>
                  </el-dropdown>
                  <el-dropdown>
                    <i class="el-icon-caret-bottom" style="cursor: pointer;"></i>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item>黄金糕</el-dropdown-item>
                      <el-dropdown-item>狮子头</el-dropdown-item>
                      <el-dropdown-item>螺蛳粉</el-dropdown-item>
                      <el-dropdown-item disabled>双皮奶</el-dropdown-item>
                     <span style="display:block;" @click="logout">
                        <el-dropdown-item divided>注销</el-dropdown-item>
                      </span>
                    </el-dropdown-menu>
                  </el-dropdown>
                </div>
              </el-card>
            </div>
            <el-main>
              <transition>
                <router-view v-if="isRouterAlive"></router-view>
              </transition>
            </el-main>
            <el-footer style="background-color: #FFFFFF;">
              <div style="line-height: 10px;">
                <span><p><strong>版权所有:</strong>Sandalen</p></span>
                <span><strong>github:</strong><a href="https://github.com/whl6785968">https://github.com/whl6785968</a></span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span><strong>blog:</strong>https://blog.csdn.net/qq_34661106</span>
              </div>
              
            </el-footer>
          </el-container>
        </el-col>
      </el-row>
  </div>
</template>

<script>
  import { removeToken } from '@/utils/auth'
  import breadcrumb from '@/components/breadcrumb.vue'
  export default {
    data(){
      return {
        activeIndex: '2',
        isCollapse:false,
        ee: 200,
        mContainer:190,
        isRouterAlive:true
      }
    },
    provide(){
      return {
        reload: this.reload
      }
    },
    methods: {
      collapse(){
        this.isCollapse = !this.isCollapse
        if(this.isCollapse == true){
          this.ee = 54
//        this.mContainer = 44
        }else{
          this.ee = 200
//        this.mmContainer = 190
        }
      },
      logout(){
        removeToken()
        this.$router.push('/login')
        this.$message.success("登出成功")
      },
      reload(){
        this.isRouterAlive = false
        this.$nextTick(function() {
          this.isRouterAlive = true
        })
      }
    },
    computed: {
      routes() {
//      alert(JSON.stringify(this.$store.state.routes))
        return this.$store.state.routes
      }
    },
    components: {
      'breadcrumb': breadcrumb
    }
  }
</script>

<style>
  /*.el-header{*/
    /*position: fixed;
    top: 0;
    right: 0;
    position: relative;
    left: 200px;

  }*/
  
  .header-card{
    position: relative;
    overflow: hidden;
    height: 60px;
    margin-left: 0;
    margin-right: -10px;
    background: #fff;
    box-shadow: 0 1px 4px rgba(0,21,41,.08);
  }
  
  .el-footer {
    position: fixed;
    bottom: 0;
    left: 200px;
    right: 0;
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
  .el-aside{
    /*width: 200px !important;*/
    height: 100%;
    position: fixed;
    font-size: 0;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 1001;
    overflow: hidden;  
  }
  
  .el-aside .el-menu-vertical-demo {
    border: none;
    height: 100%;
    width: 100% !important;
  }
  
  .el-main {
    /*position: fixed;
    top:60px;
    right: 0;
    left: 200px;
    bottom: 60px;*/
    min-height: 100%;
    transition: margin-left .28s;
/*    margin-left: 190px;*/
    margin-right: -10px;
    position: relative;
    background-color: #f0f2f5;
  }
  
  .humbger-container{
    /*padding: 0px 10px;*/
     margin-top: -1px;
    line-height: 10px;
    height: 100%;
    float: left;
    cursor: pointer;
    transition: background .3s;
  }
  
  .main-container{
    position: relative;
    margin-top: 0;
  }
  
  .avatar-container{
    margin-right: 30px;
    display: inline-block;
    /*padding: 0 3px;*/
   
    height: 100%;
    vertical-align: text-bottom;
    position: fixed;
    right: -5px;
    top: 13px;
  }
  
  .user-avatar{
    cursor: pointer;
    width: 40px;
    height: 40px;
    border-radius: 10px;
    border-style: none;
  }
  
  .breadcrumb-container{
    float: left;
    display: inline-block;
    font-size: 14px;
    margin-left: 15px;
    margin-top: 5px;
  }
</style>


