import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import user from './modules/user'

const store = new Vuex.Store({
  state: {
    routes: []
  },
  mutations: {
     initMenu(state, menus){
      state.routes = menus
    }
  },
  modules: {
    user
  }
})

export default store



